import React, { useState } from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { Container, IconButton, Drawer, List, ListItem, ListItemText, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import { KeyboardArrowDown, KeyboardArrowRight, Menu as MenuIcon, ExpandMore } from '@mui/icons-material';

const Header = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [drawerOpen, setDrawerOpen] = useState(false); //    
    const [expanded, setExpanded] = useState(false); //     Drawer

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const isMenuOpen = Boolean(anchorEl);

    const toggleDrawer = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerOpen(open);
    };

    const handleAccordionToggle = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    //    Drawer     
    const handleMenuItemClick = () => {
        setDrawerOpen(false);
    };

    //       
    const mobileMenu = (
        <Box
            sx={{ width: 250 }}
            role="presentation"
            onKeyDown={toggleDrawer(false)} // Drawer      
        >
            <List>
                <ListItem button component="a" onClick={handleMenuItemClick}>
                    <ListItemText primary="" />
                </ListItem>
                <ListItem button component="a" onClick={handleMenuItemClick}>
                    <ListItemText primary=" " />
                </ListItem>
                {/*     */}
                <Accordion expanded={expanded === 'panel1'} onChange={handleAccordionToggle('panel1')}>
                    <AccordionSummary
                        expandIcon={<ExpandMore />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography></Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <List>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="    " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="   " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="   (  )" />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="   " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="-" />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="  " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="  " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary=" " />
                            </ListItem>
                            <ListItem button onClick={handleMenuItemClick}>
                                <ListItemText primary="  " />
                            </ListItem>
                        </List>
                    </AccordionDetails>
                </Accordion>
                <ListItem button component="a" onClick={handleMenuItemClick}>
                    <ListItemText primary="" />
                </ListItem>
                <ListItem button component="a" onClick={handleMenuItemClick}>
                    <ListItemText primary="" />
                </ListItem>
            </List>
        </Box>
    );

    return (
        <>
            <AppBar position="static">
                <Container>
                    <Toolbar>
                        {/*     */}
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1, fontWeight: 'bold' }}>
                            TOPMK
                        </Typography>

                        {/*    */}
                        <Box sx={{ display: { xs: 'none', md: 'block' } }}>
                            <Button sx={{ marginRight: '6px' }} color="inherit">
                                
                            </Button>
                            <Button sx={{ marginRight: '6px' }} color="inherit">
                                 
                            </Button>

                            {/*     "" */}
                            <Button
                                sx={{ marginRight: '6px' }}
                                color="inherit"
                                aria-controls="services-menu"
                                aria-haspopup="true"
                                onClick={handleClick}
                                endIcon={isMenuOpen ? <KeyboardArrowDown /> : <KeyboardArrowRight />}
                            >
                                
                            </Button>
                            <Menu
                                id="services-menu"
                                anchorEl={anchorEl}
                                open={isMenuOpen}
                                onClose={handleClose}
                            >
                                <MenuItem onClick={handleClose} component="a">
                                        
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                       
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                       (  )
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                       
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                    -
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                      
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                      
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                     
                                </MenuItem>
                                <MenuItem onClick={handleClose} component="a">
                                      
                                </MenuItem>
                            </Menu>

                            <Button sx={{ marginRight: '6px' }} color="inherit">
                                
                            </Button>
                            <Button sx={{ marginRight: '6px' }} variant="contained" color="inherit">
                                
                            </Button>
                        </Box>

                        {/*     */}
                        <Box sx={{ display: { xs: 'block', md: 'none' } }}>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                edge="start"
                                onClick={toggleDrawer(true)}
                            >
                                <MenuIcon />
                            </IconButton>
                        </Box>
                    </Toolbar>
                </Container>
            </AppBar>

            {/* Drawer    */}
            <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
                {mobileMenu}
            </Drawer>
        </>
    );
};

export default Header;
